import AppComp from './components/AppComp';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import { NavigationContainer } from '@react-navigation/native';
import Ionicons from "react-native-vector-icons/Ionicons";
import AppScreen from './screens/AppScreen';
import {SectionList, Text, StyleSheet} from 'react-native'
import JailbreakScreen from './screens/JailbreakScreen';
import TweakedApps from './screens/TweakedApps';
import { useEffect } from 'react';
import Settings from './screens/Settings';
const Tab = createBottomTabNavigator();

export default function App() {
  return (
    <NavigationContainer>
    <Tab.Navigator
        screenOptions={({ route }) => ({
          tabBarIcon: ({ focused, color, size }) => {
            let iconName;

            if (route.name === 'Jailbreaks') {
              iconName = focused
                ? 'logo-dropbox'
                : 'logo-dropbox';
            } else if (route.name === 'Apps') {
              iconName = focused ? 'ios-albums-outline' : 'ios-albums-outline';
            } else if (route.name === 'Tweaks') {
              iconName = focused ? 'ios-hammer-outline' : 'ios-hammer-outline';
            } else if (route.name === 'Settings') {
              iconName = focused ? 'ios-cog' : 'ios-cog';
            }

            // You can return any component that you like here!
            return <Ionicons name={iconName} size={size} color={color} />;
          },
          tabBarActiveTintColor: '#007AFF',
          tabBarInactiveTintColor: 'gray',
        })}
        >
        <Tab.Screen name="Apps" component={AppScreen} />
        <Tab.Screen name="Jailbreaks" component={JailbreakScreen} />
        <Tab.Screen name="Tweaks" component={TweakedApps} />
        <Tab.Screen name="Settings" component={Settings} />
    </Tab.Navigator>
    </NavigationContainer>
  );

}
